import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { FaFilter } from "react-icons/fa";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";
import { toast } from "react-toastify";

import evaluationTypeAPI from "api/common/evaluation-type";

export default function TableFilter({
  currentFilter,
  handleEvaluationTypeFilterOnChange,
  handleFilterOnClick,
}) {
  const { t } = useTranslation();

  const [evaluationTypes, setEvaluationTypes] = useState([]);

  useEffect(async () => {
    try {
      const evaluationTypesRes = await evaluationTypeAPI.getAll();
      setEvaluationTypes(evaluationTypesRes.contents);
    } catch (err) {// eslint-disable-next-line
      
    }
  }, []);

  return (
    <Form className="d-flex flex-wrap">
      <FormGroup>
        <Label for="evaluationType">
          {t("admin.evaluation_activity_list.EVALUATION_TYPE")}
        </Label>
        <Input
          type="select"
          value={
            currentFilter.evaluationType !== null
              ? currentFilter.evaluationType
              : -1
          }
          onChange={handleEvaluationTypeFilterOnChange}
        >
          <option value={-1}>{t("admin.common.ALL")}</option>
          {evaluationTypes &&
            evaluationTypes.map((item, index) => {
              return (
                <option key={index} value={item.id}>
                  {item.ten}
                </option>
              );
            })}
        </Input>
      </FormGroup>
      &emsp;
      <FormGroup className="align-self-end">
        <Button onClick={handleFilterOnClick}>
          <FaFilter /> {t("admin.common.FILTER")}
        </Button>
      </FormGroup>
    </Form>
  );
}
